body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    display: inline;
    /* position: fixed; */
    top: 0px;
    width: 100%;
}
  
.navbarul {
    list-style-type: none;
}
  
.navitem {
    display: inline-block;
    padding: 10px;
}

.navitem > a {
    text-decoration: none;
    color: aliceblue;
}
  
.navicon {
    font-size: 30px;
    padding-left: 7px;
    padding-right: 7px;
}
  
.navitem > a:hover {
    color: grey;
}
  
.my-icon {
    display: none;
    padding: 10px;
    margin-left: auto;
    font-size: 20px;
}
  
.rightside {
    float: right;
    margin-left: auto;
}
  
@media screen and (max-width: 600px) {
    .navbarul {
      display: none;
    }
    .my-icon {
      /* float: right; */
      display: block;
      color: white;
      font-size: 30px;
    }
}
  
@media screen and (max-width: 600px) {
    .rightside {
      float: none;
      margin-left: auto;
    }
    .active {
      display: block;
    }
    .activeitems {
      display: block;
      /* text-align: left; */
      background: darkgray;
      -webkit-flex: 1 1;
              flex: 1 1;
    }
    .navbarflex {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
    }
    .navicon {
      display: block;
    }
}
.tc {
    text-align: center;
}

.resume-component {
    margin-top: 50px;
    padding-top: 50px;
}

.resume-header {
    font-family: 'Quicksand', sans-serif;
    font-size: 35px;
    font-weight: bold;
    text-align: center;
    color: black;
}

.resume-grid {
    display: grid;
    grid-template-rows: 400px 300px 400px;
    grid-template-columns: 1fr 100px 1fr;
    grid-template-areas: "education-1 map experience-1"
                         "education-2 map experience-2"
                         ".           map experience-3";
    grid-gap: 0px;
}

.line-graph {
    grid-area: map;
}

.education-1 {
    grid-area: education-1;
    height: 200px;
    margin-left: auto;
    margin-top: 40px;
}

.education-2 {
    grid-area: education-2;
    margin-top: 190px;
    margin-left: auto;
}

.experience-1 {
    grid-area: experience-1;
    margin-top: 180px;
}

.experience-2 {
    grid-area: experience-2;
    margin-top: 50px;
}

.experience-3 {
    grid-area: experience-3;
    margin-top: 70px;
}

@media screen and (max-width: 1000px) {
    .resume-grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
        grid-template-areas: "education-1"
                             "education-2"
                             "experience-1"
                             "experience-2"
                             "experience-3";
    }
    .line-graph {
        display: none;
    }
    .education-1 {
        margin-top: 15px;
        margin-right: auto;
    }
    .education-2 {
        margin-top: 15px;
        margin-right: auto;
    }
    .experience-1 {
        margin-top: 15px;
    }
    .experience-2 {
        margin-top: 15px;
    }
    .experience-3 {
        margin-top: 15px;
    }
    .resume-header {
        font-size: 25px;
    }
}
.resume-card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /* border: 1px solid gray; */
    width: 400px;
    background: #efefef;
    /* box-shadow: 3px 3px 5px 6px #ccc; */
}

h5 {
    margin: 5px;
}

.title {
    background: black;
    color: white;
    padding-left: 10px;
}

.company {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: black;
    padding-left: 10px;
}

.description {
    color: black;
    padding-left: 10px;
}

@media screen and (max-width: 1000px) {
    .resume-card {
        width: 300px;
        margin-left: auto;
        margin-right: auto;
    }
    .sbleft:before {
        display: none;
    }
    .sbright:before {
        display: none;
    }
}
.Projects-component {
    color: black;
}

.projects-header {
    font-family: 'Quicksand', sans-serif;
    font-size: 35px;
    font-weight: bold;
    text-align: center;
}

.projects-container {
    min-height: 400px;
    width: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.project-container {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 300px;
    width: 300px;
    background-color: white;
    font-family: 'Space Mono', monospace;
    font-size: 20px;
    border-radius: 10px;
    box-shadow: 3px 3px 5px 6px #ccc;
    margin: 10px;
}

/* Make images grow on hover. */
.project-image {
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s; /* Animation */
}

.project-image:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.project-image:hover .below-header {
    background-color: rgb(40, 40, 211);
}

.project-image {
    width: 200px;
    height: auto;
}

@media screen and (max-width: 600px) {
    .project-image {
        height: 180px;
        width: auto;
    }
    .project-container {
        height: 350px;
        width: 290px;
    }
}
